import { defineStore } from "pinia";
import { ref } from "vue";

import { getEcommTransactions as sendGetEcommTransactionsRequest } from "../api/ecomm-transactions.api";
import { mapTransactionsFromRequest } from "../api/ecomm.mapper";

import { EcommTransactionsRef } from "../model/ref/transactions";
import { EcommTransactionForTable } from "../model/ecomm-transaction";

export type EcomTransactionsState = {
  isFetching: boolean;
  transactions: EcommTransactionForTable;
  total: number;
};

export const useEcommTransactionsStore = defineStore(
  "ecomm-transactions",
  () => {
    const isFetching = ref(false);
    const transactions = ref<EcommTransactionForTable[]>([]);
    const total = ref(0);

    async function getEcommTransactions(
      ref: EcommTransactionsRef,
      filters?: any
    ) {
      try {
        isFetching.value = true;
        const response = await sendGetEcommTransactionsRequest(filters);

        transactions.value = mapTransactionsFromRequest(
          response.list,
          ref?.fields
        );

        total.value = response.totalCount;
        isFetching.value = false;
      } catch (error) {
        console.error(error);
      } finally {
        isFetching.value = false;
      }
    }

    return {
      isFetching,
      transactions,
      total,
      getEcommTransactions,
    };
  }
);
